.strikethrough {
  position: relative;
  color: grey;
  font-size: 14px;
}
.strikethrough:before {
  position: absolute;
  content: "";
  left: 0;
  top: 50%;
  right: 0;
  border-top: 1px solid;
  border-color: inherit;

  -webkit-transform:rotate(-5deg);
  transform:rotate(10deg);
}

