/* Banner */
.banner-box {
  max-width: 100%;
  display: grid;
  grid-template-columns: auto auto auto;
  overflow-x: hidden;  
}
.banner-box img {
  margin: 15px; 
  height: 290px;
  object-fit: cover;
  width: auto;
}
/* popular Categories */

.popular-categories {
  overflow-x: hidden;  
  margin: 24px;
}

.popular-categories h2 {
  margin-bottom: 24px;
  font-family: "Open Sans", sans-serif;
}
.popular-categories .category-grid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 24px;
  
  width: 100%;
  margin: 0px auto;
}
.popular-categories .category {
  position: relative;
}
.popular-categories .category img {
  width: 30vw;
  height: auto;
  object-fit: cover;
}
.popular-categories .category-textbox {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: absolute;
  top: 0px;
  left: 0px;
  height: 50%;
  margin: 40px;
}
.popular-categories .title {
  font-size: 18px;
}
.popular-categories .offer {
  color: #888888;
}
.popular-categories .shop--now {
  font-size: 14px;
}
/* Horizontal banner */
.slim-wide-banner {
  width: 96%;
  height: auto;
  margin: 24px;
}

@media only screen and (max-width: 1280px) {
  .banner-box img {
    margin: 15px; 
    object-fit: cover;
    height: 270px;
    margin-top: 20px;
  }
}
